export const gameTickHandler = (e, gameObjects) => {

  if (!gameTickVarsDefined) {
    container = gameObjects.getKeyValue('container')
    gameWidth = gameObjects.getKeyValue('game-width')
    weaponsUI = gameObjects.getKeyValue('weapons-ui')
    render = gameObjects.getKeyValue('render')
    groundTex = gameObjects.getKeyValue('ground-texture')
    groundBody = gameObjects.getKeyValue('ground')
    groundFx = gameObjects.getKeyValue('static-ground-effects')
    playerBody = getPlayerPartByType('body', gameObjects)
    acceleration = getState().game.player.variables.acceleration.min
    // pixi layer objects
    pistolShells = gameObjects.getKeyValue('pistol-shells')
    pistolBullets = gameObjects.getKeyValue('pistol-bullets')
    bulletImpacts = gameObjects.getKeyValue('bullet-impacts')
    ammoDrops = gameObjects.getKeyValue('ammo-drops-by-type')
    creditDrops = gameObjects.getKeyValue('currency-drops')
    healthdrops = gameObjects.getKeyValue('health-drops')
    pistolAmmoDrops = ammoDrops[WEAPON_TYPE_PISTOL]
    smgAmmoDrops = ammoDrops[WEAPON_TYPE_SMG]
    shotgunAmmoDrops = ammoDrops[WEAPON_TYPE_SHOTGUN]
    // make crosshair
    crosshairs = new Crosshairs(gameObjects)
    crosshair = crosshairs.crosshair
    gameObjects.setKeyValue('crosshair-shape', crosshairs.crosshair)
    gameObjects.setKeyValue('crosshair', crosshair)
    // laser graphics
    // laserBlur = new PIXI.Graphics()
    // if (getState().game.filterEffects) {
    //   laserBlur.filters = [ blurFilter ]
    // }
    // laser = new PIXI.Graphics()
    // zombies left / right counters
    leftEnemyCountDisplay = new PIXI.Text('')
    leftEnemyCountDisplay.position = { x: 10, y: 5 }
    rightEnemyCountDisplay = new PIXI.Text('zombies on right: 0')
    rightEnemyCountDisplay.pivot.x = rightEnemyCountDisplay.width * .075
    rightEnemyCountDisplay.position = { x: gameWidth, y: 5 }
    // player 
    player = gameObjects.getKeyValue('player')
    // enemies
    zombies = gameObjects.getKeyValue('zombies')
    zombieRags = gameObjects.getKeyValue('zombie-ragdolls')
    // gibs
    gibs = gameObjects.getKeyValue('gibs')
    // grenades
    grenades = gameObjects.getKeyValue('grenades')
    explosions = gameObjects.getKeyValue('explosions')
    rocks = gameObjects.getKeyValue('rocks')
    woodPlanks = gameObjects.getKeyValue('wood-planks')
    // traps
    landmines = gameObjects.getKeyValue('landmines')
    // build objects
    buildObjects = gameObjects.getKeyValue('build-objects')
    // fx
    explosionSprites = gameObjects.getKeyValue('explosion-sprites')
    sparkSprites = gameObjects.getKeyValue('spark-sprites')
    // container
    container = gameObjects.getKeyValue('game-pixi-container')
    // container.addChild(laserBlur)
    // container.addChild(laser)
    wallTile = new WallTile()
    // let d = new DropShadowFilter()
    // d.
    // groundTex.filters = [
    //   // new DropShadowFilter({ rotation: 0, distance: 20, blur: 1, alpha: 10 })
    //   new GlowFilter({ outerStrength: 100, color: 0x0 })
    // ]
    let gradientAlpha = gameObjects.getKeyValue('gradient-alpha')
    gradientAlpha.alpha = .75
    container.addChild( wallTile, gradientAlpha, groundTex )
    player.addSpritesInOrder()
    container.addChild( leftEnemyCountDisplay )
    container.addChild( rightEnemyCountDisplay )
    // vars declared?
    gameTickVarsDefined = true
  }

  let {
    game: {
      keys,
      // width: gameWidth,
      // height: gameHeight,
      gameState,
      gameSubState,
      mouseState,
      mousePosition,
      player: {
        variables: { magnetism, magnetismSpeed, currentDirection, jumping, acceleration: { max: accelMax }, deceleration },
        weapons: { laser: laserSight, equipped, totalAmmo: { grenades: grenadesCount } }
      },
      enemies: {
        zombies: {
          zombieAttackLimiter,
          zombieAttackDistance
        }
      },
      traps: {
        turret: { fireDistance }
      },
      mouseState: { x: mx, y: my, btn: mouseButtonState }
    }
  } = getState()
  let mPosRel = {
    x: mousePosition.x + playerBody.position.x - (width / 2),
    y: mousePosition.y + playerBody.position.y - (height / 2)
  }
  dispatch(setMouseRelativeToPlayerPosition({ x: mPosRel.x, y: mPosRel.y }))

  // Render pixi graphics in-line with matter objects
  const renderPositionFn = part => {
    return {
      x: part.position.x - playerBody.position.x + (width / 2) + (currentDirection === 'right' ? acceleration : -acceleration),
      y: part.position.y - playerBody.position.y + (height / 2),
      rotation: part.angle
    }
  }

  if (gameState === GameStates.GAMEPLAY) {

    // render tile bg
    wallTile.renderPosition(renderPositionFn, playerBody, acceleration)
    // wallTile.onUpdate()

    // Player weapons
    // check if firing
    if (mouseButtonState === MOUSE_DOWN && !player.meleeAttackInProgress && !player.grenadeThrowInProgress) {
      // check weapon type
      if (equipped === WEAPON_TYPE_PISTOL) { firePistol(gameObjects) }
      if (equipped === WEAPON_TYPE_SMG) { fireSMG(gameObjects) }
      if (equipped === WEAPON_TYPE_SHOTGUN) { fireShotgun(gameObjects) }
    }

    // console.log(width, height)

    // Set render view to follow player
    Render.lookAt(render, {
      min: { x: playerBody.position.x + (width / 2), y: playerBody.position.y - (height / 2) },
      max: { x: playerBody.position.x - (width / 2), y: playerBody.position.y + (height / 2) }
    })

    // Player movement / controls
    Body.setAngle(playerBody, 0)

    if (!weaponsUI.reloading && !player.meleeAttackInProgress && !player.grenadeThrowInProgress) {
      // movement
      if (keys.indexOf(68) > -1 || keys.indexOf(65) > -1) {
        if (acceleration < accelMax) {
          acceleration += .25
        }
      } else {
        dispatch(setLastDirection(currentDirection))
        if (acceleration > 0) {
          acceleration -= deceleration
        }
      }

      if (keys.indexOf(68) > -1 && keys.indexOf(65) > -1) {
        if (acceleration > 0) {
          acceleration -= deceleration
        }
      }

      if (keys.indexOf(68) > -1) {
        dispatch(setCurrentDirection('right'))
        Body.translate(playerBody, { x: acceleration, y: 0 })
      }
      if (keys.indexOf(65) > -1) {
        dispatch(setCurrentDirection('left'))
        Body.translate(playerBody, { x: -acceleration, y: 0 })
      }

      // grenades
      if (keys.indexOf(71) > -1 && grenadesCount > 0 && player && !player.meleeAttackInProgress && !weaponsUI.reloading) {
        if (gameSubState !== SubStates.SHOP_QUERY_SUBMENU && gameSubState !== SubStates.BUILD_PLACEMENT_MODE) {
          player.chargeGrenade()
        }
      }
      if (getState().game.rightMouseDown && grenadesCount > 0 && player && !player.meleeAttackInProgress && !weaponsUI.reloading && !jumping) {
        if (gameSubState !== SubStates.SHOP_QUERY_SUBMENU && gameSubState !== SubStates.BUILD_PLACEMENT_MODE) {
          player.chargeGrenade()
          console.log('should charge grenade from engineHandlers')
        }
      }
    }

    let { x: playerPosX, y: playerPosY } = renderPositionFn(playerBody)
    dispatch(setPlayerPosition({ x: playerPosX, y: playerPosY }))

    // render health drops
    healthdrops.forEach(health => {
      health.renderPosition(renderPositionFn)
    })

    // render ammo boxes
    pistolAmmoDrops.concat(
      smgAmmoDrops
    ).concat(
      shotgunAmmoDrops
    ).forEach(box => {
      box.positionAmmoBox(renderPositionFn)
      if (box.body && box.body.position.y > 500) {
        box.destroy()
      }
      if (box.body) {
        if (playerBody.position.x > box.body.position.x && ((playerBody.position.x - box.body.position.x) < magnetism)) {
          Body.translate(box.body, { x: magnetismSpeed, y: 0 })
        }
        if (playerBody.position.x < box.body.position.x && ((box.body.position.x - playerBody.position.x) < magnetism)) {
          Body.translate(box.body, { x: -magnetismSpeed, y: 0 })
        }
      }
    })

    // render currency drops
    creditDrops.forEach(coin => {
      coin.positionCreditGfx(renderPositionFn)
      if (coin.creditBody && coin.creditBody.position.y > 500) {
        coin.destroy()
      }
      if (coin.creditBody) {
        if (playerBody.position.x > coin.creditBody.position.x && ((playerBody.position.x - coin.creditBody.position.x) < magnetism)) {
          if (playerBody.position.x > coin.creditBody.position.x && ((playerBody.position.x - coin.creditBody.position.x) < (magnetism/2))) {
            Body.translate(coin.creditBody, { x: (magnetismSpeed * 2), y: 0 })
          } else {
            Body.translate(coin.creditBody, { x: magnetismSpeed, y: 0 })
          }
        }
        if (playerBody.position.x < coin.creditBody.position.x && ((coin.creditBody.position.x - playerBody.position.x) < magnetism)) {
          if (playerBody.position.x < coin.creditBody.position.x && ((coin.creditBody.position.x - playerBody.position.x) < (magnetism/2))) {
            Body.translate(coin.creditBody, { x: -(magnetismSpeed * 2), y: 0 })
          } else {
            Body.translate(coin.creditBody, { x: -magnetismSpeed, y: 0 })
          }
        }
      }
    })

    // render player graphics
    player.setBodySpritesPositions({ mx, fn: renderPositionFn })
    player.setArmsSpritePosition({ mx, my, fn: renderPositionFn })
    player.positionHeadSprite({ mx, my, fn: renderPositionFn })
    player.renderGrenadeChargeMeter(renderPositionFn)

    if (player.charRunSprite && player.charIdleSprite && player.charJumpSprite) {

      if (keys.indexOf(65) > -1 || keys.indexOf(68) > -1) {
        if (!jumping) {
          player.charRunSprite.play()
          player.charRunSprite.visible = true
          player.charIdleSprite.visible = false
          player.stopJumpAnimation()
        }
      } else {
        player.charRunSprite.stop()
        player.charRunSprite.visible = false
        player.charIdleSprite.visible = true
        // added below lines to stop sprites overlapping
        player.armsSprites.forEach(sprite => { sprite.visible = false })
        player.reloadAnims.forEach(sprite => { sprite.visible = false })
      }

      if (
        (keys.indexOf(65) > -1 && mouseState.x === GREATER_THAN_PLAYER_X_POS) ||
        (keys.indexOf(68) > -1 && mouseState.x === LESS_THAN_PLAYER_X_POS)
      ) {
        player.charRunSprite.animationSpeed = -1
      } else {
        player.charRunSprite.animationSpeed = 1
      }

      if (mouseState.x === GREATER_THAN_PLAYER_X_POS) {
        player.bodySprites.forEach(sprite => { sprite.scale.x = -1 })
        player.armsSprites.forEach(sprite => { sprite.scale.y = -1 })
        player.headSprite.scale.y = -1
      } else {
        player.bodySprites.forEach(sprite => { sprite.scale.x = 1 })
        player.armsSprites.forEach(sprite => { sprite.scale.y = 1 })
        player.headSprite.scale.y = 1
      }

      if (weaponsUI.reloading) {
        player.bodySprites.forEach(sprite => { sprite.visible = false })
        player.armsSprites.forEach(sprite => { sprite.visible = false })
        player.reloadAnimsByType[equipped].visible = true
        player.charIdleSprite.visible = false
      } else {
        if (player.armsSpritesByType[equipped]) {
          player.armsSprites.forEach(sprite => { sprite.visible = false })
          player.armsSpritesByType[equipped].forEach(sprite => {
            sprite.visible = true
          })
          player.reloadAnimsByType[equipped].visible = false
        }
      }

      if (jumping || weaponsUI.reloading || player.meleeAttackInProgress || player.grenadeThrowInProgress) {
        player.headSprite.visible = false
      } else {
        player.headSprite.visible = true
      }

      if (!jumping) {
        player.stopJumpAnimation()
      } else {
        player.charIdleSprite.visible = false
        if (player.charJumpSprite.currentFrame < 2) {
          player.armsSpritesByType[equipped].forEach(sprite => {
            sprite.y += 80
          })
        }
      }

      if (player.meleeAttackInProgress) {
        player.armsSprites.forEach(sprite => { sprite.visible = false })
        player.bodySprites.forEach(sprite => {
          if (player.meleeSprites.indexOf(sprite) < 0) {
            sprite.visible = false
          }
        })
      }

      if (player.grenadeThrowInProgress) {
        player.armsSprites.forEach(sprite => { sprite.visible = false })
        player.bodySprites.forEach(sprite => {
          if (player.grenadeSprites.indexOf(sprite) < 0) {
            sprite.visible = false
          }
        })
      }

      // --- begin laser
      // if (!player.dead && laserSight[equipped] && !player.grenadeThrowInProgress && !player.meleeAttackInProgress && !weaponsUI.reloading) {
      //   if (laser) {
      //     laser.clear()
      //     laserBlur.clear()
      //   }
      //   let { x, y } = renderPositionFn(player.armPointBody)
      //   let color = getState().game.player.modTiers['sight'][equipped].current > 2 ? 0x11DD67 : 0xFF0000
      //   laserBlur.lineStyle(2, color, .35)
      //   laserBlur.moveTo(x, y)
      //   laserBlur.lineTo(mousePosition.x, mousePosition.y)
      //   laser.lineStyle(1, color, .75)
      //   laser.moveTo(x, y)
      //   laser.lineTo(mousePosition.x, mousePosition.y)
      // } else {
      //   if (laser) {
      //     laser.clear()
      //     laserBlur.clear()
      //   }
      // }
      // --- end laser

    }

    // render player ragdoll
    if (gameObjects.getKeyValue('player-ragdoll') !== null) {
      let playerRag = gameObjects.getKeyValue('player-ragdoll')
      if (playerRag && playerRag.animateRagParts) {
        playerRag.animateRagParts(renderPositionFn)
      }
    }

  } // -- end if gameState === GameState.GAMEPLAY

  if (player.body.position.y > 500) {
    player.kill(FELL_OFF)
  }

  // render pistol shells
  for (let i = 0; i < pistolShells.length; ++i) {
    if (!pistolShellWd) { pistolShellWd = getBodyWidth(pistolShells[i].body) }
    if (!pistolShellHt) { pistolShellHt = getBodyHeight(pistolShells[i].body) }
    pistolShells[i].gfc.position.x = pistolShells[i].body.position.x - playerBody.position.x + (width / 2) + (currentDirection === 'right' ? acceleration : -acceleration)
    pistolShells[i].gfc.position.y = pistolShells[i].body.position.y - playerBody.position.y + (height / 2)
    pistolShells[i].gfc.rotation = equipped === WEAPON_TYPE_SHOTGUN ? pistolShells[i].body.angle + degrees(90) : pistolShells[i].body.angle
  }
  // render pistol bullets
  for (let i = 0; i < pistolBullets.length; ++i) {
    if (!pistolBulletWd) { pistolBulletWd = getBodyWidth(pistolBullets[i].body) }
    if (!pistolBulletHt) { pistolBulletHt = getBodyHeight(pistolBullets[i].body) }
    pistolBullets[i].gfc.position.x = pistolBullets[i].body.position.x - playerBody.position.x + (width / 2) + (currentDirection === 'right' ? acceleration : -acceleration)
    pistolBullets[i].gfc.position.y = pistolBullets[i].body.position.y - playerBody.position.y + (height / 2)
    pistolBullets[i].gfc.rotation = pistolBullets[i].body.angle
    // draw bullet trail effect
    // pistolBullets[i].linePoints.push({
    //   x: pistolBullets[i].gfc.position.x,
    //   y: pistolBullets[i].gfc.position.y
    // })
    // pistolBullets[i].drawLine()
    // pistolBullets[i].bulletTrail.alpha -= .05
  }

  // render grenades
  grenades.forEach(grenade => {
    grenade.animate(renderPositionFn)
  })

  // explosions tracking
  explosions.forEach(explosion => {
    explosion.calcRenderedPosition(renderPositionFn)
  })
  rocks.forEach(rock => { rock.animate(renderPositionFn) })
  woodPlanks.forEach(wood => { wood.animate(renderPositionFn) })

  // update ejected mags
  player.updateEjectedMagPositions(renderPositionFn)

  // render gibs / guts 
  gibs.forEach(gib => {
    gib.animate(renderPositionFn)
  })

  // for zombie count radar
  leftEnemyCount = 0
  rightEnemyCount = 0
  // dispatch( setZombiesOnLeft(leftEnemyCount) )
  // dispatch( setZombiesOnLeft(rightEnemyCount) )

  // keep zombie arm angle inline with player position
  zombies.forEach(zombie => {
    zombie.setPlayerVars({ player, playerDirection: currentDirection, playerBody })
    if (!zombie.paused) {
      zombie.walk()
    }
    Body.setAngle(zombie.body, 0)
    zombie.setSpritesPositions(renderPositionFn)
    // kill zombies who fall off the ledge
    if (zombie.body.position.y > 500) {
      zombie.kill()
    }
    // trigger zombie attack
    if (
      (playerBody.position.x > zombie.body.position.x && ((playerBody.position.x - zombie.body.position.x) < zombieAttackDistance)) ||
      (playerBody.position.x < zombie.body.position.x && ((zombie.body.position.x - playerBody.position.x) < zombieAttackDistance))) {
      if (zombie.timeSinceLastAttack + zombieAttackLimiter < Date.now()) {
        zombie.attack()
      }
    }
    // trigger attack if next to a wall
    buildObjects.forEach(obj => {
      if (obj && !obj.destroyed && obj.getBody().label !== 'remote mine trap') {
        // zombie attack barrier control 
        if (
          (obj.getBody().position.x > zombie.body.position.x && ((obj.getBody().position.x - zombie.body.position.x) < zombieAttackDistance)) ||
          (obj.getBody().position.x < zombie.body.position.x && ((zombie.body.position.x - obj.getBody().position.x) < zombieAttackDistance))) {
          if (zombie.timeSinceLastAttack + zombieAttackLimiter < Date.now()) {
            if (obj.getBody().label !== 'landmine trap') {
              zombie.attack()
            }
          }
        }
        // turret fire control
        if (
          obj.direction && obj.direction === 'right' && zombie.body.position.x > obj.getBody().position.x - 10 && zombie.body.position.x - obj.getBody().position.x < fireDistance ||
          obj.direction && obj.direction === 'left' && zombie.body.position.x < obj.getBody().position.x - 10 && obj.getBody().position.x - zombie.body.position.x < fireDistance
          ) {
            obj.shoot()
        }
      }
    })
    // count zombies on left and right side of player
    if (!zombie.dead) {
      if (zombie.body.position.x < playerBody.position.x) {
        leftEnemyCount += 1
        // dispatch( setZombiesOnLeft(leftEnemyCount) )
      }
      if (zombie.body.position.x > playerBody.position.x) {
        rightEnemyCount += 1
        // dispatch( setZombiesOnRight(rightEnemyCount) )
      }
    }
  })
  
  // build traps and barriers
  buildObjects.forEach(obj => {
    if (obj && !obj.destroyed && obj.displayBarrierStrength) {
      obj.displayBarrierStrength(renderPositionFn)
    }
    if (obj.updatePosition) {
      obj.updatePosition(renderPositionFn)
    }
  })

  // double-check in case radar counts are inaccurate
  if (getState().game.waveCountToDefeat === 0 || getState().game.waveCountToDefeat === null) {
    // for zombie count radar
    leftEnemyCount = 0
    rightEnemyCount = 0
    // dispatch( setZombiesOnLeft(leftEnemyCount) )
    // dispatch( setZombiesOnRight(rightEnemyCount) )
  }

  // update zombie count on sides of player
  leftEnemyCountDisplay.text = `${leftEnemyCount}`
  leftEnemyCountDisplay.style = getZombieRadarTextStyle(leftEnemyCount)
  rightEnemyCountDisplay.text = `${rightEnemyCount}`
  rightEnemyCountDisplay.style = getZombieRadarTextStyle(rightEnemyCount)

  zombieRags.forEach(rag => {
    rag.animateRagParts(renderPositionFn)
  })

  // render crosshair
  if (gameSubState !== SubStates.PLAYER_DEATH) {
    crosshair.position = mousePosition
  }

  if (crosshair.changed) {
    crosshair = crosshair._new
    crosshair.changed = false
  }

  // if crosshair is an arrow..
  if (crosshair.changeBack) {
    container.removeChild(crosshair)
    crosshair = crosshairs.crosshair
    container.addChild(crosshairs.crosshair)
    crosshair.changeBack = false
  }

  // ground
  groundTex.position = renderPositionFn(groundBody)

  // bullet impacts
  bulletImpacts.forEach(impact => { impact.renderPosition(renderPositionFn) })
  groundFx.forEach(fx => { fx.renderPosition(renderPositionFn) })

  // fx
  explosionSprites.forEach(ex => { ex.renderPosition(renderPositionFn) })
  sparkSprites.forEach(spark => { spark.renderPosition(renderPositionFn) })

  // keep cursor on top
  container.addChild(crosshairs.crosshair)

}
